.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19,19,18,0.8);
}
.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
}
.footer-left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.footer-right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location div{
    margin-bottom: 0.8rem;
}
.location p{
    line-height: 30px;
}
h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
}
@media screen and (max-width:640px){
    .footer-container{
        display: grid;
        grid-template-columns:1fr;
    }
}
